import BG from 'story/BG.json';

export const DesktopWarn = () => {
  return (
    <>
      <h2
        className="overlapBlock"
        style={{
          height: '100%',
          width: '100%',
          zIndex: 100,
          color: '#ffffff',
          margin: 0,
        }}
      >
        Please watch on Mobile
      </h2>
      <img src={BG[0].img} alt="novell" height="100%" width="100%" />
    </>
  );
};
