/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { Suspense, useState } from 'react';
import { useInitial } from 'hooks';
import { useSelector } from 'react-redux';
import { getGlobal, getPersonsLoaded } from 'store';
import { Styles } from 'Styles';
import { Spinner } from 'components';
import { DesktopWarn } from 'containers';

const Scene = React.lazy(() => import('containers/Scene'));

interface Props {
  width: string;
  height: string;
}

function App(props: Props) {
  useInitial(parseInt(props.width, 10), parseInt(props.height, 10));
  const [play, setPlay] = useState(false);

  const { height, width, isMobile } = useSelector(getGlobal);
  const isPersonsLoaded = useSelector(getPersonsLoaded);

  return (
    <>
      <Styles />
      <div
        className="container"
        onClick={() => {
          setPlay(true);
        }}
      >
        {isMobile ? (
          <Suspense fallback={<Spinner />}>
            {play || !isPersonsLoaded ? null : (
              <div
                className="overlapBlock"
                style={{
                  height,
                  width,
                }}
                onClick={(e) => {
                  e.preventDefault();
                }}
              >
                <button type="button" className="playBtn">
                  Click to play
                </button>
              </div>
            )}
            <Scene play={play} />
          </Suspense>
        ) : (
          <DesktopWarn />
        )}
      </div>
    </>
  );
}

export default App;
